import React, { useState, useEffect } from "react";
import ".././components/Photos.css";
import Down from "./Down";

var url =
  "https://pixabay.com/api/?key=15039565-c2e62dfd6711de941df28667f&q=blue+flowers&image_type=photo&pretty=true";
function Photos() {
  const [photos, setphotos] = useState([]);
  const getPhotos = async () => {
    const resp = await fetch(url);
    const photos = await resp.json();
    setphotos(photos);
    console.log(photos);
  };
  useEffect(() => {
    getPhotos(url);
    return () => {};
  }, []);

  const searchImg = () => {
    var Search = document.querySelector("#inp").value;
    var num = document.querySelector("#num").value;
    url = `https://pixabay.com/api/?key=15039565-c2e62dfd6711de941df28667f&q=" +
      ${Search} +
      "&image_type=photo&pretty=true&per_page=${num}`;
    console.log(url);
    getPhotos(url);
  };
  let itemsToRender;
  if (photos.hits) {
    itemsToRender = (
      <div id="mainbox">
        <div className="titl">
          <h1>Picosa</h1>
          <br />
          <p>explore the world in a click</p>
        </div>
        <div className="searchbox">
          <input
            id="inp"
            type="text"
            onKeyUp={(event) => {
              if (event.keyCode === 13) {
                // Cancel the default action, if needed
                event.preventDefault();
                // Trigger the button element with a click
                document.getElementById("myBtn").click();
              }
            }}
          />
          <button id="myBtn" onClick={searchImg}>
            search
          </button>
          <br />
          <br />
          <input type="text" placeholder="number of images" id="num" />
        </div>
        {photos.hits.map((pic) => {
          const {
            id,
            downloads,

            user,
            largeImageURL,
            userImageURL,
            webformatURL,
          } = pic;

          return (
            <div key={id} className="box">
              <a href={largeImageURL} download>
                <img
                  src={webformatURL}
                  alt={downloads}
                  className="main"
                  download
                />
              </a>
              <div className=" txt">
                <a href={userImageURL}>
                  <img src={userImageURL} alt="" />
                </a>
                <div className="usertext">
                  {user} <br />
                  {downloads} downloads
                </div>
                <Down link={webformatURL} />
              </div>
            </div>
          );
        })}
      </div>
    );
  } else {
    itemsToRender = "Loading...";
  }
  return <div>{itemsToRender}</div>;
}

export default Photos;
