import React from "react";
import { NavLink } from "react-router-dom";
import Photos from "../Photos";

import "./Navbar.css";

function NavBar() {
  return (
    <>
      <div id="nav" className="vc">
        <div className="logo">
          <div id="logo"></div>
          <label htmlFor="logo"></label>
        </div>
        <ul>
          <li>
            <NavLink className="menu-item" to="./home">
              Home
            </NavLink>
          </li>
          <li>
            <NavLink className="menu-item" to="/Video">
              Video
            </NavLink>
          </li>
          <li>
            <NavLink className="menu-item" to="/help">
              Help
            </NavLink>
          </li>
          <li>
            <NavLink className="menu-item" to="/more">
              More
            </NavLink>
          </li>
        </ul>
      </div>
    </>
  );
}
export default NavBar;
